import React from 'react';

import connect from '@vkontakte/vk-connect';


import {Panel, Alert, Group, PanelHeader, Cell, Avatar, Button, List, Gallery} from "@vkontakte/vkui"
import Icon24Money from '@vkontakte/icons/dist/24/money_circle';
import Icon24UserOut from '@vkontakte/icons/dist/24/user_outgoing';


class HomePanelBase extends React.Component {


    state = {
        groups: {
            admined: [],
            other: [],
        },
        loading: true,
        errorGetAuthToken: false,
        showImg: false,
        firstName: "",
        lastName: "",
        ava: "",
        _id:"5d8fdaf14827a83f988d5e00",
        statusOld:"1",
        statusNew:"0",
        maxCount:"",
        result:""
    };

    showImg = () => {
        this.setState({showImg: true});
    };


    route(){
      this.setState({activePanel: 'home'})
    }

    //async serverGet(){
      //let result = await fetch(`https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getAllChallenges/update/`+this.state._id+`/`+this.state.statusNew, {

      //this.forceUpdate();
//}
    async componentDidMount() {
      const profile = await connect.sendPromise("VKWebAppGetUserInfo");

      this.state.firstName = profile.first_name;
      this.state.lastName = profile.last_name;
      this.state.ava = profile.photo_200;
      let result = await fetch(`https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getAllChallenges/inactive`, {
      method: "GET",
      // body: JSON.stringify(''),
    })
      result=await result.json();
      console.log(result);
      this.state.maxCount = result.challenge.length;
      this.state.result = result.challenge;
      console.log(this.state.result[0]);

      this.forceUpdate();



    }
    render() {
        const {setStory, activeStory} = this.props;
        const {id} = this.props;
        const items = [];
        for(let i = 0;i < this.state.maxCount;i++)
        items.push(<Group key={i}>
          <Gallery
            slideWidth="90%"
            align="center"
            style={{ height: 150 }}
            className="galleryElement"
          >
            <div>
              <p className="name">{this.state.result[i].name}</p>
              <p className="description">{this.state.result[i].body}</p>
              <Button level="secondary" before={<Icon24UserOut/>}></Button>
            </div>
          </Gallery>
        </Group>);

        return (
            <Panel id={id}>
                <PanelHeader> </PanelHeader>
                <Group>
                  <Cell
                    photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                    description="VKontakte"

                    before={<Avatar src={this.state.ava} size={80}/>}
                    size="l"
                  >
                    {this.state.firstName} {this.state.lastName}
                  </Cell>
                </Group>
                {items}
            </Panel>
        );
    }

}



export default HomePanelBase;
