import React from 'react';
import Popup from "reactjs-popup";
import connect from '@vkontakte/vk-connect';

import {setPage} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";

import Icon24Money from '@vkontakte/icons/dist/24/money_circle';
import Icon24UserOut from '@vkontakte/icons/dist/24/user_outgoing';
import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    FixedLayout,
    HorizontalScroll,
    TabsItem,
    Tabs,
    Gallery,
    Button
} from "@vkontakte/vkui";

class HomePanelProfile extends React.Component {
    state = {
      result:"",
      maxCount:"",
      _id:"",
      idLink:""
    };

    constructor(props) {
        super(props);

        this.state = {


        };
    }

    setTab(tab) {
        this.setState({
            activeTab: tab
        });
    }

    getdetails(obj) {
        this.state.idLink = obj.id;
        console.log(this.state.idLink);
    }

    async sendPay() {

      const pays = await connect.send("VKWebAppOpenPayForm", {"app_id": 7150912, "action": "transfer-to-group", "params": { "group_id": 187017371}});

    }

    async componentDidMount() {
        restoreScrollPosition();

          let result = await fetch('https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getAllChallenges/active', {
          method: "GET",
          // body: JSON.stringify(''),
        })
        result=await result.json();

        this.state.maxCount = result.challenge.length;
        this.state.result = result.challenge;

        console.log(this.state.result[1]);
        console.log(this.state.maxCount);
        this.forceUpdate();
    }
    async serverPush(e) {
        restoreScrollPosition();

          let result = await fetch(`https://demo146.bravo.vkhackathon.com/api/v1/challangeContainer/getAllChallenges/update/`+e+`/0`, {
          method: "GET",
          // body: JSON.stringify(''),
        })
        result=await result.json();

        this.state.maxCount = result.challenge.length;
        this.state.result = result.challenge;

        console.log(this.state.result[1]);
        console.log(this.state.maxCount);
        this.forceUpdate();
        document.location.reload(true);
    }


    render() {
        const {id, setPage} = this.props;
        const boxStyle = {marginTop: 56};

        const items = [];

        for(let i = 0;i < this.state.maxCount;i++)

        items.push(<Group key={i}>
          <Gallery

            slideWidth="90%"
            align="center"
            style={{ height: 150 }}
            className="galleryElement"
          >
            <div>
              <p className="name">{this.state.result[i].name}</p>
              <p className="description">{this.state.result[i].body}</p>
              <Button level="secondary" before={<Icon24UserOut/>}></Button>
              <Button  id={this.state.result[i]._id} level="commerce" className="challengeButton" before={<Icon24Money/>} onClick={e => this.sendPay()&&this.serverPush(this.state.result[i]._id)}></Button>

            </div>
          </Gallery>
        </Group>);

        return (
            <Panel id={id}>
                <PanelHeader noShadow={true}></PanelHeader>
                <FixedLayout vertical="top" className="headerMenu">
                    <Tabs theme="header" type="buttons">
                        <HorizontalScroll id="EXAMPLE_TABS_LIST">
                            <TabsItem
                                onClick={() => this.setTab('modal')}
                                selected={this.state.activeTab === 'modal'}
                            >
                                
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test')}
                                selected={this.state.activeTab === 'test'}
                            >
                                
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test2')}
                                selected={this.state.activeTab === 'test2'}
                            >
                                
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test3')}
                                selected={this.state.activeTab === 'test3'}
                            >
                                
                            </TabsItem>
                        </HorizontalScroll>
                    </Tabs>
                </FixedLayout>
                {items}
            </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID
};

export default HomePanelProfile;
